.tweetsPage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

}

.backBtn {
    padding: 10px;
    cursor: pointer;

}

.button {
    margin-left: auto;
    margin-right: auto;
    background-color: #ebd8ff;
    border-radius: 10.3px;
    color: #373737;
    width: 196px;
    height: 50px;
    padding: 14px, 28px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 1, 22%;
    cursor: pointer;
}

.button:hover,
.button:focus {
    background-color: rgb(75, 64, 234);
}

.title {
    color: #373737;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 1, 22%;
}

.text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    color: #373737;
    ;
}